<template>
  <v-container>
    <v-row>
      <v-col cols="12" class="py-2">
        <p class="mt-4 mb-0">Title</p>
        <v-chip-group
          active-class="nordicSky"
          column
          outlined
          v-model="title"
          mandatory
        >
          <v-chip
            v-for="title in titles"
            :key="title"
            class="select-btn pa-5"
            :value="title"
            color="white"
          >
            {{ title }}
          </v-chip>
        </v-chip-group>
      </v-col>
      <v-col cols="12">
        <p class="mb-1">First name</p>
        <v-text-field
          color="nordicSky"
          background-color="white"
          v-model="firstname"
        ></v-text-field>
      </v-col>
      <v-col cols="12">
        <p class="mt-0 mb-3">Surename</p>
        <v-text-field
          v-model="surename"
          color="nordicSky"
          background-color="white"
          :rules="textFieldRules"
        ></v-text-field>
      </v-col>
      <v-col cols="12">
        <p class="mt-0 mb-3">Date of birth</p>
        <v-menu
          ref="menu"
          v-model="menu"
          :close-on-content-click="false"
          transition="scale-transition"
          offset-y
          min-width="auto"
          background-color="white"
        >
          <template v-slot:activator="{ on, attrs }">
            <v-text-field
              v-model="date"
              label="Birthday date"
              prepend-inner-icon="mdi-calendar"
              readonly
              color="nordicSky"
              v-bind="attrs"
              filled
              background-color="white"
              v-on="on"
            ></v-text-field>
          </template>
          <v-date-picker
            v-model="date"
            color="nordicSky"
            :active-picker.sync="activePicker"
            :max="(new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10)"
            min="1950-01-01"
            @change="save"
          ></v-date-picker>
        </v-menu>
      </v-col>
      <v-col cols="12">
        <p class="mb-3">Marital Status</p>
        <v-select
          :items="maritalStatuses"
          filled
          outlined
          hide-details
          color="grey"
          class="text-field"
        ></v-select>
      </v-col>
      <v-col cols="12">
        <p class="mt-0 mb-3">Occupation</p>
        <v-text-field
          v-model="occupation"
          color="nordicSky"
          background-color="white"
          hide-details
          class="text-field"
        ></v-text-field>
      </v-col>
      <v-col cols="12">
        <p class="mt-0 mb-3">Industry</p>
        <v-text-field
          v-model="industry"
          color="nordicSky"
          background-color="white"
          hide-details
          class="text-field"
        ></v-text-field>
      </v-col>
      <v-col cols="12" class="py-2">
        <p class="mt-4 mb-3">Would you like to include a second applicant?</p>
        <v-btn-toggle v-model="secondApplicant">
          <v-btn value="true" class="select-btn"> Yes </v-btn>
          <v-btn value="false" class="select-btn"> No </v-btn>
        </v-btn-toggle>
      </v-col>
      <v-col cols="12" class="py-2">
        <p class="mt-4 mb-3">
          Have you or a member of your family made a claim in the last 5 years?
        </p>
        <v-btn-toggle v-model="hasPreviousClaim" background-color="white">
          <v-btn value="true" class="select-btn"> Yes </v-btn>
          <v-btn value="false" class="select-btn"> No </v-btn>
        </v-btn-toggle>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  name: "AboutYou",
  data() {
    return {
      title: "",
      firstname: "",
      textFieldRules: [
        (v) => v.length <= 25 || "Max 25 characters",
        (v) => /^[a-z\d\-_\s]+$/.test(v) || "Invalid character entered.",
      ],
      surename: "",
      dob: {
        day: "",
        month: "",
        year: "",
      },
      dobDayRules: [ (v) => v < 1 && v > 31 || "Error" ],
      titles: ["Mr", "Mrs", "Miss", "Mx", "Ms", "Doctor", "Rev"],
      maritalStatuses: ["Single", "Married", "Divorced"],
      occupation: "",
      industry: "",
      secondApplicant: false,
      hasPreviousClaim: false,
      activePicker: null,
      date: null,
      menu: false,
    };
  },
  watch: {
    menu (val) {
      val && setTimeout(() => (this.activePicker = 'YEAR'))
    },
  },
  methods: {
    save (date) {
      this.$refs.menu.save(date)
    },
  },
};
</script>

<style lang="sass">
  @import "~vuetify/src/components/VTextField/_variables.scss"

  $text-field-border-radius: 10px
  $text-field-padding: 20px 15px


  @import "~vuetify/src/components/VTextField/VTextField.sass"
</style>
